@import 'variables';

.body {
    display: block;
    box-sizing: border-box;
    margin: 0;
    width: 100%;
    height: 100%;
    background-color: $bodyColor;
}

// Reset styles

h1, h2, h3, h4, h5, p {
    margin: 0;
    padding: 0;
}

button {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

.preload * {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
  }

.container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    max-width: $siteWight;
    margin: 0 auto;
}
